@import 'src/styles/variables';

.summary {
  background: $black;
  height: 100vh;
  position: relative;
}

.summary .mid-panel {
  position: absolute;
  top: 5%;
  left: 5%;
  height: 90%;
  width: 90%;
  overflow: hidden;
}

.summary .mid-panel::before {
  background-size: 3%;
  background-repeat: round;
  background-image: url(../../assets/wave.svg);
  content: '';
  position: absolute;
  overflow: hidden;
  width: 150%;
  height: 200%;
  top: -50%;
  left: -50%;
  transform: rotate(30deg);
}

.summary .right-panel {
  background-size: 100% 100%;
  background-image: linear-gradient(
    to right,
    $white,
    transparent 1px,
    transparent 100%
  );
  position: absolute;
  right: 0;
  height: 100%;
  width: 5%;
}

.summary .content {
  color: $white;
  font-family: $font-default;
  position: relative;
  text-align: center;
  z-index: 10;
  text-align: left;
  top: 17.5%;
}

@media (min-width: 768px) {
  .summary .content {
    width: 80%;
    height: 70%;
    top: 15%;
    margin-left: auto;
    margin-right: auto;
  }
}

@media (min-width: 0px) and (max-width: 767px) {
  .summary .content {
    width: 90%;
    height: 80%;
    top: 10%;
    margin-left: auto;
    margin-right: auto;
  }

  .summary .right-panel {
    background-size: 100% 100%;
    background-image: linear-gradient(transparent 0%, transparent 0%);
  }

  .summary .content > .content-left {
    position: absolute;
    left: 0%;
    height: 100%;
    width: 100% !important;
  }

  .summary .content > .content-right {
    display: none !important;
    position: absolute;
    left: 0%;
    height: 100%;
    width: 100% !important;
  }

  .top-navbar > .navbar-content > .left {
    left: 15px !important;
    top: 30px !important;
    position: absolute;
  }

  .right-navbar > .top-right-corner {
    position: absolute;
    top: 40px !important;
    right: 40px !important;
  }

  .right-navbar > .navbar-content {
    display: none;
  }

  .info-line-0,
  .info-line-1 {
    font-size: 3em !important;
  }

  .info-line-2,
  .info-line-3,
  .info-line-4,
  .info-line-5 {
    font-size: 1.1em !important;
  }
}

.summary .content > .content-left {
  position: absolute;
  left: 0%;
  height: 100%;
  width: 60%;
}

.summary .content > .content-right {
  position: absolute;
  left: 60%;
  height: 100%;
  width: 40%;
}

.top-navbar {
  color: $white;
  display: block;
  height: 12.5vh;
  position: absolute;
  top: 0;
  width: 100%;
  z-index: 10;
}

.top-navbar > .navbar-content {
  height: 100%;
  position: relative;
}

.top-navbar > .navbar-content > .left {
  left: 20px;
  top: 30px;
  position: absolute;
}

.right-navbar {
  display: block;
  height: 100%;
  position: absolute;
  top: 0;
  right: 0;
  width: 5%;
  z-index: 10;
}

.right-navbar > .top-right-corner {
  position: absolute;
  top: 50px;
}

.right-navbar > .navbar-content {
  color: $white;
  top: 50%;
  right: 50%;
  position: absolute;
  transform: translate(50%, -50%);
  writing-mode: vertical-rl;
  text-orientation: mixed;
}

.right-navbar > .navbar-content > a {
  color: $white;
  font-size: 1em;
  margin: 2vh 0 2vh 0;
}

.right-navbar > .navbar-content > a:hover {
  border-left: 2px solid $green;
  padding-left: 2px;
}

.info-line-0,
.info-line-1 {
  font-size: 4.5em;
  font-weight: 800;
  line-height: 1.2em;
}

.info-line-2 {
  display: flex;
  align-items: center;
  font-size: 1.3em;
  font-weight: 500;
  margin-top: 1em;
  line-height: 2.1em;
}

.info-line-2::before {
  display: inline;
  content: '{ ';
  margin-right: 0.25em;
  color: $green;
  font-size: 1.6em;
}

.info-line-4::after {
  content: ' }';
  margin-left: 0.25em;
  color: $green;
  font-size: 1.6em;
}

.info-line-3 {
  display: flex;
  align-items: center;
  font-size: 1.3em;
  font-weight: 500;
  line-height: 2.1em;
}

.info-line-4 {
  display: flex;
  align-items: center;
  font-size: 1.3em;
  font-weight: 500;
  line-height: 2.1em;
}

.info-line-5 {
  display: flex;
  align-items: center;
  font-size: 1.3em;
  font-weight: 500;
  line-height: 2em;
  padding-top: 20px;
}

.summary .icon {
  display: inline;
  margin: 0 2px 0 2px;
  width: 1.6em;
}

@media (min-width: 768px) {
  .summary .programming-img {
    width: 25vw;
    position: absolute;
    bottom: 0%;
    right: 0%;
  }
}

@media (min-width: 0px) and (max-width: 767px) {
  .summary .programming-img {
    width: 150px;
    position: absolute;
    bottom: 0%;
    right: 0%;
  }
}

h1 {
  display: inline;
  font-family: $font-default;
  font-size: 1.1em;
  padding-left: 5px;
  padding-right: 5px;
}

h2 {
  color: $red;
  display: inline;
  font-family: $font-serif;
  font-size: 1.4em;
}

h4 {
  display: inline;
  font-family: $font-default;
  font-size: 1em;
}

h5 {
  color: $red;
  display: inline;
  font-size: 1em;
}

@media (min-width: 768px) {
  .globe {
    background: $black;
    border: 2px solid $green;
    border-radius: 50%;
    overflow: hidden;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    height: 60px;
    width: 60px;
    // z-index: 20;

    svg {
      animation: rotation 5s linear 0s infinite;
      path {
        stroke: $green;
      }
    }
  }
}

@media (max-width: 768px) {
  .globe {
    background: $black;
    border: 2px solid $green;
    border-radius: 50%;
    overflow: hidden;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    height: 40px;
    width: 40px;
    // z-index: 20;

    svg {
      animation: rotation 5s linear 0s infinite;
      path {
        stroke: $green;
      }
    }
  }
}

@keyframes rotation {
  from {
    transform: translateX(0);
  }

  from {
    transform: translateX(-50%);
  }
}

.shine-on-hover:hover {
  color: $green;
}

.wavy-underlined {
  background: url('../../assets/wave-green.svg');
  background-repeat: repeat;
  background-position-x: 0%;
  background-position-y: 50%;
  background-size: 0.4em auto;
  background-repeat: repeat-x;
  background-position: 0px 1em;
  animation: move 10s linear infinite;
  animation-play-state: paused;
  text-decoration: none;
  background-color: transparent;
}

.wavy-underlined:hover {
  animation-play-state: running;
}

@keyframes move {
  from {
    background-position: 0px 1.1em;
  }
  to {
    background-position: 500px 1em;
  }
}
